.stars {
  display: flex;

  &__star {
    background-image: url("../images/star.svg");
    background-position: center;
    background-repeat: no-repeat;
    border-radius: 0.5px;
    margin-right: 4px;
    margin-top: 1px;
    width: 16px;
    height: 16px;
  }

  &__star:last-child {
    margin-right: 0;
  }

  @for $index from 1 through 5 {
    &--#{$index} .stars__star:nth-child(-n + #{$index}) {
      background-image: url("../images/star-active.svg");
    }
  }
}
